@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&family=Righteous&display=swap');


*{
    color: white;
    font-family: 'Inconsolata', monospace;
    font-size: 18px;
}




body{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    top:50%;
    left:50%;
    position: absolute;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    text-align: center;
    border: 10px solid black;
    background: black;
    border-radius: 5px;
}

.keypad{
    display: grid;
    grid-template-columns:repeat(4,1fr) ;
    grid-auto-rows: minmax(60px,auto);
}
input[type="text"]{
    height: 75px;
    width: 249px;
    background-color: black;
    border:0px;
    color:white;
    text-align: right;
    font-size: 25px;
    font-weight:500;
    letter-spacing: 1px;
}
#clear{
    grid-column: 1/3;
    grid-row: 1;
    color:white;
}
#result{
    grid-column: 3/5;
    grid-row: 5;
    color: white;
}
#backspace{
    color:white;
}
button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: #262834;
    color: white;
    font-weight: 500;
}
input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}
.highlight{
    background: rgba(34, 166, 205, 1.0);
    background: -webkit-linear-gradient(top left, rgba(34, 166, 205, 1.0), rgba(164, 98, 236, 1.0));
    background: -moz-linear-gradient(top left, rgba(34, 166, 205, 1.0), rgba(164, 98, 236, 1.0));
    background: linear-gradient(to bottom right, rgba(34, 166, 205, 1.0), rgba(164, 98, 236, 1.0));
}
